import { USER_LOGOUT ,USER_LOGIN_FAIL,USER_LOGIN_REQUEST,USER_LOGIN_SUCCESS,
USER_REGISTER_FAIL,USER_REGISTER_REQUEST,USER_REGISTER_SUCCESS} from "../constants/UserConstants";
import axios from "axios";

export const login = (email,password) => async (dispatch) => {

    try {

          dispatch ({type:USER_LOGIN_REQUEST})

          const config = {
            
            headers:{

                "Content-type":"application/json",
            },

          }

          const {data} = await axios.post(`http://localhost:4000/api/users/login`,{email,password},config);
          dispatch({type:USER_LOGIN_SUCCESS,payload:data});

          localStorage.setItem("userInfo",JSON.stringify(data));
        
    } catch (error) {

        dispatch ({

            type : USER_LOGIN_FAIL,
            payload:
             error.response &&   error.response.data.message 
             ? error.response.data.message 
             : error.message,
        });
        
    }
}

//LOGOUT

export const logout = () => (dispatch) =>{

    localStorage.removeItem("userInfo");
    dispatch({type:USER_LOGOUT});
    document.location.href ="/Login";
} ;


//REGISTER

export const register = (userName,email,password) => async (dispatch) => {

    try {

          dispatch ({type:USER_REGISTER_REQUEST})

          const config = {
            
            headers:{

                "Content-type":"application/json",
            },

          }

          const {data} = await axios.post(`http://localhost:4000/api/users`,{userName,email,password},config);
          dispatch({type:USER_REGISTER_SUCCESS,payload:data});

          localStorage.setItem("userInfo",JSON.stringify(data));
        
    } catch (error) {

        dispatch ({

            type : USER_REGISTER_FAIL,
            payload:
             error.response &&   error.response.data.message 
             ? error.response.data.message 
             : error.message,
        });
        
    }
}